import React, {useState} from 'react';
import {
    Gallery,
    Header,
    Placeholder,
    Banner,
    Panel,
    PanelHeader,
    Card,
    CardGrid,
    PanelHeaderBack,
    Button,
    MiniInfoCell,
    Caption,
    Subhead,
    TabsItem,
    Tabs,
    Div,
    HorizontalScroll,
    Title,
    Avatar,
    Cell,
    RichCell,
    Footer, Separator
} from "@vkontakte/vkui";
import './Home.css'

import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon20WorkOutline from '@vkontakte/icons/dist/20/work_outline';
const Cart = ({ id, go, fetchedUser, activeItem, onDecrementPosition, onIncrementPosition, order }) => {
    const getOrder = (order) => {
        let count = 0;
        let sum = 0;
        for (let key in order){
            count = count + order[key].count;
            sum = sum + order[key].count * order[key].item.price
        }
        if (count === 0){
            return <Cell><Caption>Ваша корзина пуста</Caption></Cell>
        } else {
            return <Cell><Caption>Всего товаров - {count}, на сумму {sum} р.</Caption></Cell>
        }
    }

    const renderOrderList = (order) => {
        let arr = [];
        for (let key in order) {
            arr.push(
                <React.Fragment>
                    <RichCell
                        before={<Avatar size={92} src={order[key].item.img}/>}
                        text={
                            <MiniInfoCell after={<span style={{color: 'red'}}>Удалить</span>}>
                                <button
                                    className="Place__food-button"
                                    onClick={() => {
                                        onDecrementPosition(order[key].item);
                                    }}
                                >
                                    -
                                </button>
                                <span>&nbsp;{key in order ? order[key].count : 0}&nbsp;</span>
                                <button
                                    className="Place__food-button"
                                    onClick={() => {
                                        onIncrementPosition(order[key].item);
                                    }}
                                >
                                    +
                                </button>
                            </MiniInfoCell>
                        }
                        caption={<MiniInfoCell before={<Icon24MoneyCircle height={20} width={20}/>}><Subhead>{order[key].item.price} руб.</Subhead></MiniInfoCell>}
                    >
                        <MiniInfoCell textWrap={'full'}>{order[key].item.name}</MiniInfoCell>
                    </RichCell>
                    <Separator/>
                </React.Fragment>
            )
        }
        return arr
    }
    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
            {renderOrderList(order)}
            {getOrder(order)}
            <Footer/>
        </Panel>
    )
};

export default Cart
