import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Category from './panels/Category'
import AboutItem from "./panels/AboutItem"
import Cart from './panels/Cart'
import {Div, FixedLayout, Placeholder, Spinner} from "@vkontakte/vkui";

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(null); //<ScreenSpinner size='large' />
	const [targetCategory, setTargetCategory] = useState(null);
	const [activeItem, setActiveItem] = useState(null);
	const [activeSubcat, setActiveSubcat] = useState(null);
	const [arrItems, setArrItems] = useState([])
	const [filtredArrItem, setFiltredArrItem] = useState(null)

	const [ orderStatuses, setOrderStatuses ] = useState(JSON.parse((localStorage.getItem('orderStatuses') || 'null')) || {});
	const [ order, setOrder ] = useState(JSON.parse((localStorage.getItem('orders') || 'null')) || {});

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);

			setPopout(null);
		}
		fetchData();
	}, []);

	const go = panelName => {
		setActivePanel(panelName);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' order={order} fetchedUser={fetchedUser} go={go} setTargetCategory={setTargetCategory}/>
			<Category id='category'
					  order={order}
					  arrItems={arrItems}
					  setArrItems={setArrItems}
					  filtredArrItem={filtredArrItem}
					  setFiltredArrItem={setFiltredArrItem}
					  activeSubcat={activeSubcat}
					  setActiveSubcat={setActiveSubcat}
					  fetchedUser={fetchedUser}
					  go={go}
					  targetCategory={targetCategory}
					  setActiveItem={setActiveItem}
					  onIncrementPosition={item => {
						  const updatedOrder = {...order};

						  if (item.id in updatedOrder) {
						  	updatedOrder[item.id].count++;
						  } else {
						  	updatedOrder[item.id] = {
								  item: item,
								  count: 1,
							  };
						  }
						  console.log(updatedOrder)
							const serialized = JSON.stringify(updatedOrder);
						  localStorage.setItem('orders', serialized);
						  setOrder(updatedOrder);
					  }}
					  onDecrementPosition={item => {
						  const updatedOrder = {...order};

						  if (item._id in updatedOrder) {
							  if (updatedOrder[item._id].count === 1) {
								  delete updatedOrder[item._id];
							  } else {
								  updatedOrder[item._id].count--;
							  }
						  }
							console.log(updatedOrder)
						  const serialized = JSON.stringify(updatedOrder);
						  localStorage.setItem('orders', serialized);
						  setOrder(updatedOrder);
					  }}
			/>
			<AboutItem
				id='aboutItem'
				order={order}
				fetchedUser={fetchedUser}
				go={go}
				targetCategory={targetCategory}
				activeItem={activeItem}
				onIncrementPosition={item => {
					const updatedOrder = {...order};

					if (item.id in updatedOrder) {
						updatedOrder[item.id].count++;
					} else {
						updatedOrder[item.id] = {
							item: item,
							count: 1,
						};
					}
					const serialized = JSON.stringify(updatedOrder);
					localStorage.setItem('orders', serialized);
					setOrder(updatedOrder);
					setPopout(<ScreenSpinner/>);
					setTimeout(function () {
						setPopout(null)
					}, 1000);
				}}
				onDecrementPosition={item => {
					const updatedOrder = {...order};
					if (item.id in updatedOrder) {
						if (updatedOrder[item.id].count === 1) {
							delete updatedOrder[item._id];
						} else {
							updatedOrder[item.id].count--;
						}
					}
					const serialized = JSON.stringify(updatedOrder);
					localStorage.setItem('orders', serialized);
					setOrder(updatedOrder);
				}}
			/>
			<Cart
				id={'cart'}
				go={go}
				order={order}
				onIncrementPosition={item => {
					const updatedOrder = {...order};

					if (item.id in updatedOrder) {
						updatedOrder[item.id].count++;
					} else {
						updatedOrder[item.id] = {
							item: item,
							count: 1,
						};
					}
					const serialized = JSON.stringify(updatedOrder);
					localStorage.setItem('orders', serialized);
					setOrder(updatedOrder);
				}}
				onDecrementPosition={item => {
					const updatedOrder = {...order};
					if (item.id in updatedOrder) {
						if (updatedOrder[item.id].count === 1) {
							delete updatedOrder[item._id];
						} else {
							updatedOrder[item.id].count--;
						}
					}
					const serialized = JSON.stringify(updatedOrder);
					localStorage.setItem('orders', serialized);
					setOrder(updatedOrder);
				}}
			/>
		</View>
	);
}

export default App;

