import React, {useState} from 'react';
import {
    Gallery,
    Header,
    Placeholder,
    Banner,
    Panel,
    PanelHeader,
    Card,
    CardGrid,
    PanelHeaderBack,
    Button,
    MiniInfoCell,
    Caption,
    Subhead,
    TabsItem,
    Tabs,
    Div,
    HorizontalScroll,
    Title,
    Group,
    Cell,
    FixedLayout,
    Footer
} from "@vkontakte/vkui";
import './Home.css'

import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle'
import Icon20WorkOutline from '@vkontakte/icons/dist/20/work_outline'

const AboutItem = ({ id, go, fetchedUser, activeItem, onIncrementPosition, order }) => {
    const getOrder = (order) => {
        let count = 0;
        let sum = 0;
        console.log(order)
        for (let key in order){
            count = count + order[key].count;
            sum = sum + order[key].count * order[key].item.price
        }
        return <Cell before={<Button onClick={()=>go('cart')} before={<Icon20WorkOutline/>} size={'m'}>Корзина</Button>}><Caption>Товаров - {count}, на сумму {sum} руб.</Caption></Cell>
    }
    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={()=>go('category')} />}/>
            {
                activeItem !== null &&
                <React.Fragment>
                    <Group header={<Header mode="secondary">Информация о товаре</Header>}>
                        {/*<Gallery*/}
                        {/*    slideWidth="90%"*/}
                        {/*    style={{ height: 200 }}*/}
                        {/*    bullets="dark"*/}
                        {/*>*/}
                        {/*    <div style={{ backgroundColor: '#FFF', backgroundImage: 'url('+activeItem.img+')', backgroundPosition: 'center',*/}
                        {/*        backgroundRepeat: 'no-repeat' }}/>*/}
                        {/*    <div style={{ backgroundColor: 'var(--button_commerce_background)' }} />*/}
                        {/*    <div style={{ backgroundColor: '#DDD' }} />*/}
                        {/*</Gallery>*/}
                        <div style={{height: 250, backgroundColor: '#FFF', backgroundImage: 'url('+activeItem.img+')', backgroundPosition: 'center',
                            backgroundRepeat: 'no-repeat', backgroundSize: 'cover' }}/>
                    </Group>
                    <Div>
                        <CardGrid>
                            <Card size="l">
                                <MiniInfoCell textWrap={'full'} ><Title level="1" weight="semibold" style={{ marginBottom: 16 }}>{activeItem.name}</Title></MiniInfoCell>
                                <MiniInfoCell after={<Button onClick={()=>onIncrementPosition(activeItem)} mode={'outline'}>В корзину</Button>} before={<Icon24MoneyCircle height={32} width={32}/>}><Title level="1" weight="semibold">{activeItem.price} руб.</Title></MiniInfoCell>
                            </Card>
                            <Card size="l">
                                <Div style={{
                                    backgroundColor: '#03825e',
                                    borderRadius: 10,
                                    color: 'antiquewhite'
                                }}>{activeItem.description}</Div>
                            </Card>
                        </CardGrid>
                    </Div>
                </React.Fragment>
            }
            {
                order !== null &&
                <React.Fragment>
                    <Div style={{padding: 10}} />
                    <FixedLayout vertical={'bottom'}>
                        {getOrder(order)}
                    </FixedLayout>
                </React.Fragment>
            }
            <Footer/>
        </Panel>
    )
};

export default AboutItem;
